.back{
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: auto;
    top: 0;
    left: 0;
    background: rgb(64,111,113);
    background: linear-gradient(0deg, rgba(64,111,113,1) 0%, rgba(158,232,241,1) 94%);
  }
  .messenger {
      display: grid;
      width: 1000px;
      height: 800px;
      margin: 0 auto;
      padding-top: 5%;
      padding-bottom: 5%;
    
      grid-template-areas:
      "h h "
      "n c ";
    
      grid-template-rows: 60px 1fr;
      grid-template-columns: 3fr 10fr;
     
    }